import { getLenis } from 'src/plugins/lenis';
import { createSplitLines } from 'src/plugins/split-text';

export function initGalleryAnimation() {
  /* ---------------------------------------
   * DEBUG HELPERS (LOG ONLY NOT FOUND)
   * ------------------------------------- */
  const warnOnceCache = new Set();

  function warnOnce(key, message) {
    if (warnOnceCache.has(key)) return;
    warnOnceCache.add(key);
    console.warn(message);
  }

  function logIfNotFound(label, el) {
    if (!el) {
      warnOnce(label, `[GSAP NOT FOUND] ${label}`);
      return true;
    }
    return false;
  }

  /* ---------------------------------------
   * DOM ELEMENTS
   * ------------------------------------- */
  const wrapper = document.querySelector('.gallery-wrapper');
  const gallery = document.querySelector('.gallery');
  const galleryContainer = document.querySelector('.gallery-container');
  const cardsNode = document.querySelectorAll('.card');
  const block = document.querySelector('.gallery-block');

  logIfNotFound('.gallery', gallery);
  logIfNotFound('.gallery-container', galleryContainer);

  if (cardsNode.length === 0) {
    warnOnce('.card-empty', '[GSAP NOT FOUND] .card (no elements found)');
  }

  /* ---------------------------------------
   * STATE
   * ------------------------------------- */
  const cards = [];
  const transformState = [];

  const isPreviewActive = false;
  const isTransitioning = false;
  let scrollProgress = 0; // Progress animasi scroll (0-1)

  const STACK_CENTER_X = window.innerWidth / 2;
  const STACK_CENTER_Y = window.innerHeight / 2;

  let galleryExtraScaleProgress = 0;
  let galleryFinalScaleProgress = 0;
  let hoverScrollProgress = 0;
  let hoverBlend = 0; // 0 → 1

  const config = {
    radius: 0,
    sensitivity: 500,
    effectFalloff: 250,
    cardMoveAmount: 50,
    lerpFactor: 0.15,
    isMobile: window.innerWidth < 1000,
    imageCount: cardsNode.length,
  };

  // === AUTO RADIUS (16 images friendly) ===
  const IMAGE_COUNT = 16;
  const GAP_EM = 2; // Gap horizontal untuk posisi awal

  const rootFontSize = parseFloat(getComputedStyle(document.documentElement).fontSize);

  const cardWidth = cardsNode[0]?.getBoundingClientRect().width || 0;
  const gapPx = GAP_EM * rootFontSize;

  config.imageCount = IMAGE_COUNT;

  // Radius untuk formasi lingkaran
  config.radius = ((cardWidth + gapPx * 1.5) * IMAGE_COUNT) / (2 * Math.PI);

  const parallaxState = {
    targetX: 0,
    targetY: 0,
    targetZ: 0,
    currentX: 0,
    currentY: 0,
    currentZ: 0,
  };

  /* ---------------------------------------
   * CUSTOMIZE: GALLERY SCALE
   * ------------------------------------- */
  const SCALE_START = 1.75; // scale awal
  const SCALE_END = 1; // scale akhir

  /* ---------------------------------------
   * INIT CARDS - POSISI AWAL HORIZONTAL
   * ------------------------------------- */
  for (let i = 0; i < config.imageCount; i++) {
    const card = cardsNode[i];

    if (!card) {
      warnOnce(`.card-${i}`, `[GSAP NOT FOUND] .card at index ${i}`);
      continue;
    }

    const angle = (i / config.imageCount) * Math.PI * 2;

    /* ---------------------------------------
     * CUSTOMIZE: START CLUSTER
     * ------------------------------------- */
    const START_OFFSET_RATIO = 1; // posisi cluster
    const CLUSTER_SPACING = cardWidth * 0.25; // jarak antar card

    const baseX = window.innerWidth * START_OFFSET_RATIO;
    const baseY = 0;

    const initialX = baseX + i * (cardWidth + CLUSTER_SPACING);

    const initialY = baseY;

    card.dataset.index = i;

    gsap.set(card, {
      x: initialX,
      y: initialY,
      rotation: 0,
      transformPerspective: 800,
      transformOrigin: 'center center',
    });

    cards.push(card);
    transformState.push({
      angle,
      currentRotation: 0,
      targetRotation: 0,
      currentX: 0,
      targetX: 0,
      currentY: 0,
      targetY: 0,
      currentScale: 1,
      targetScale: 1,
      initialX,
      initialY,
      stackX: window.innerWidth * 0,
      stackY: 0,
    });
  }

  /* ---------------------------------------
   * CUSTOMIZE: SCROLL PHASES
   * ------------------------------------- */
  const PHASE_STACK_START = 0.0;
  const PHASE_STACK_END = 0.5;

  const PHASE_CIRCLE_START = 0.5;
  const PHASE_CIRCLE_END = 1.0;

  /* ---------------------------------------
   * HOVER ACTIVE WINDOW
   * ------------------------------------- */
  const HOVER_START = PHASE_CIRCLE_END;
  const HOVER_END = 1.15;

  function isHoverEnabled() {
    if (config.isMobile) return false;
    if (isPreviewActive || isTransitioning) return false;

    const BUFFER = 0.001;

    return hoverScrollProgress > BUFFER && hoverScrollProgress < 1 - BUFFER;
  }

  /* ---------------------------------------
   * SCROLLTRIGGER SETUP
   * ------------------------------------- */
  if (typeof ScrollTrigger !== 'undefined') {
    ScrollTrigger.create({
      trigger: wrapper,
      start: 'top center',
      end: '55% center',
      scrub: true,
      ease: 'custom',
      onUpdate: (self) => {
        scrollProgress = self.progress;
      },
    });
  } else {
    console.warn('[GSAP] ScrollTrigger not loaded');
  }

  ScrollTrigger.create({
    trigger: wrapper,
    start: '65% center',
    end: '75% center',
    duration: 1,
    scrub: true,
    ease: 'custom',
    onUpdate: (self) => {
      galleryExtraScaleProgress = self.progress;
    },
  });

  ScrollTrigger.create({
    trigger: wrapper,
    start: '75% center',
    end: 'bottom center',
    scrub: true,
    ease: 'custom',
    onUpdate: (self) => {
      galleryFinalScaleProgress = self.progress;
    },
  });

  ScrollTrigger.create({
    trigger: wrapper,
    start: '55% center',
    end: '65% center',
    scrub: true,
    onUpdate(self) {
      hoverScrollProgress = self.progress; // 0–1
    },
  });

  /* ---------------------------------------
   * RESIZE
   * ------------------------------------- */
  function handleResize() {
    const w = window.innerWidth;
    config.isMobile = w < 1000;

    let scale = 1;
    if (w < 768) scale = 0.6;
    else if (w < 1200) scale = 0.8;

    if (!logIfNotFound('.gallery', gallery)) {
      gsap.set(gallery, { scale });
    }

    if (!isPreviewActive) {
      Object.assign(parallaxState, {
        targetX: 0,
        targetY: 0,
        targetZ: 0,
        currentX: 0,
        currentY: 0,
        currentZ: 0,
      });

      transformState.forEach((state, i) => {
        state.currentRotation = 0;
        state.targetRotation = 0;
        state.currentScale = 1;
        state.targetScale = 1;
        state.currentX = 0;
        state.targetX = 0;
        state.currentY = 0;
        state.targetY = 0;
        state.stackX = window.innerWidth * 0;
        state.stackY = 0;

        const START_OFFSET_RATIO = -0.5; // posisi cluster
        const CLUSTER_SPACING = cardWidth * 0.25; // jarak antar card

        const baseX = window.innerWidth * START_OFFSET_RATIO;
        const baseY = window.innerHeight * -0.15;

        // Update posisi awal sesuai resize
        state.initialX = baseX + i * (cardWidth + CLUSTER_SPACING);
        state.initialY = baseY;
      });
    }
  }

  window.addEventListener('resize', handleResize);
  handleResize();

  /* ---------------------------------------
   * MOUSE MOVE
   * ------------------------------------- */
  document.addEventListener('mousemove', (e) => {
    if (!isHoverEnabled()) return;

    const cx = window.innerWidth / 2;
    const cy = window.innerHeight / 2;
    const px = (e.clientX - cx) / cx;
    const py = (e.clientY - cy) / cy;

    parallaxState.targetY = px * 15;
    parallaxState.targetX = -py * 15;
    parallaxState.targetZ = (px + py) * 5;

    cards.forEach((card, i) => {
      const state = transformState[i];
      if (!card || !state) {
        warnOnce(`mousemove-${i}`, `[GSAP SKIPPED] mousemove index ${i}`);
        return;
      }

      const rect = card.getBoundingClientRect();
      const dx = e.clientX - (rect.left + rect.width / 2);
      const dy = e.clientY - (rect.top + rect.height / 2);
      const dist = Math.hypot(dx, dy);

      if (dist < config.sensitivity) {
        const f = Math.max(0, 1 - dist / config.effectFalloff);
        const move = config.cardMoveAmount * f;

        state.targetRotation = 180 * f;
        state.targetScale = 1 + 0.3 * f;
        state.targetX = move * Math.cos(state.angle);
        state.targetY = move * Math.sin(state.angle);
      } else {
        state.targetRotation = 0;
        state.targetScale = 1;
        state.targetX = 0;
        state.targetY = 0;
      }
    });
  });

  /* ---------------------------------------
   * MOUSE OUT
   * ------------------------------------- */
  document.addEventListener('mouseout', (e) => {
    if (!e.relatedTarget && !isPreviewActive && !isTransitioning) {
      transformState.forEach((state) => {
        state.targetRotation = 0;
        state.targetScale = 1;
        state.targetX = 0;
        state.targetY = 0;
      });

      parallaxState.targetX = 0;
      parallaxState.targetY = 0;
      parallaxState.targetZ = 0;
    }
  });

  let wasHoverEnabled = false;

  /* ---------------------------------------
   * RAF ANIMATE
   * ------------------------------------- */
  function animate() {
    const hoverEnabled = isHoverEnabled();
    const targetHoverBlend = hoverEnabled ? 1 : 0;

    // smooth masuk & keluar hover
    hoverBlend += (targetHoverBlend - hoverBlend) * 0.1;

    // Jika barusan keluar dari hover zone → RESET
    if (wasHoverEnabled && !hoverEnabled) {
      transformState.forEach((state) => {
        state.targetRotation = 0;
        state.targetScale = 1;
        state.targetX = 0;
        state.targetY = 0;

        // state.currentRotation = 0;
        // state.currentScale = 1;
        // state.currentX = 0;
        // state.currentY = 0;
      });
    }

    wasHoverEnabled = hoverEnabled;

    console.log('scroll:', hoverScrollProgress.toFixed(2), 'hover:', isHoverEnabled());

    if (!isPreviewActive && !isTransitioning) {
      //   parallaxState.currentX +=
      //     (parallaxState.targetX - parallaxState.currentX) * config.lerpFactor;
      //   parallaxState.currentY +=
      //     (parallaxState.targetY - parallaxState.currentY) * config.lerpFactor;
      //   parallaxState.currentZ +=
      //     (parallaxState.targetZ - parallaxState.currentZ) * config.lerpFactor;

      let galleryScale = SCALE_START;

      // scale dari stack → circle
      if (scrollProgress >= PHASE_CIRCLE_START) {
        const p = gsap.utils.mapRange(PHASE_CIRCLE_START, PHASE_CIRCLE_END, 0, 1, scrollProgress);

        galleryScale = gsap.utils.interpolate(SCALE_START, SCALE_END, p);
      }

      // scale tambahan (zoom besar)
      const extraScale = gsap.utils.interpolate(1, 1.7, galleryExtraScaleProgress);

      // scale akhir (zoom final)
      const finalScale = gsap.utils.interpolate(1, 1.1, galleryFinalScaleProgress);

      const baseScale = galleryScale;

      // FINAL SCALE (dikombinasikan)
      const combinedScale = galleryScale * extraScale * finalScale;

      if (!logIfNotFound('.gallery-container', galleryContainer)) {
        gsap.set(galleryContainer, {
          rotateX: parallaxState.currentX,
          rotateY: parallaxState.currentY,
          rotation: parallaxState.currentZ,

          transformOrigin: 'center center',
        });

        if (!logIfNotFound('.gallery', gallery)) {
          gsap.set(gallery, {
            scale: combinedScale,
          });
        }
      }

      cards.forEach((card, i) => {
        const state = transformState[i];
        if (!card || !state) {
          warnOnce(`animate-${i}`, `[GSAP SKIPPED] animate index ${i}`);
          return;
        }

        // Hitung progress individual untuk setiap card (staggered)
        const cardDelay = i / config.imageCount;
        const cardProgress = Math.max(0, Math.min(1, (scrollProgress - cardDelay * 0.3) / 0.7));

        // Interpolasi dari posisi awal ke posisi lingkaran
        const circleX = config.radius * Math.cos(state.angle);
        const circleY = config.radius * Math.sin(state.angle);

        let baseX = state.initialX;
        let baseY = state.initialY;
        let baseRotation = 0;

        // PHASE 1: HORIZONTAL → STACK
        if (scrollProgress <= PHASE_STACK_END) {
          const p = gsap.utils.mapRange(PHASE_STACK_START, PHASE_STACK_END, 0, 1, scrollProgress);

          baseX = gsap.utils.interpolate(state.initialX, state.stackX, p);
          baseY = gsap.utils.interpolate(state.initialY, state.stackY, p);
          baseRotation = 0;
        }

        // PHASE 2: STACK HOLD (opsional, implicit)
        else if (scrollProgress < PHASE_CIRCLE_START) {
          baseX = state.stackX;
          baseY = state.stackY;
          baseRotation = 0;
        }

        // PHASE 3: STACK → CIRCLE
        else {
          const p = gsap.utils.mapRange(PHASE_CIRCLE_START, PHASE_CIRCLE_END, 0, 1, scrollProgress);

          const circleX = config.radius * Math.cos(state.angle);
          const circleY = config.radius * Math.sin(state.angle);

          baseX = gsap.utils.interpolate(state.stackX, circleX, p);
          baseY = gsap.utils.interpolate(state.stackY, circleY, p);

          baseRotation = ((state.angle * 180) / Math.PI + 90) * p;
        }

        // const baseRotation = ((state.angle * 180) / Math.PI + 90) * cardProgress;

        // Apply hover effects only when scroll is complete
        const hoverMultiplier = hoverBlend;

        state.currentRotation += (state.targetRotation - state.currentRotation) * config.lerpFactor;
        state.currentScale += (state.targetScale - state.currentScale) * config.lerpFactor;
        state.currentX += (state.targetX - state.currentX) * config.lerpFactor;
        state.currentY += (state.targetY - state.currentY) * config.lerpFactor;

        gsap.set(card, {
          x: baseX + state.currentX * hoverMultiplier,
          y: baseY + state.currentY * hoverMultiplier,
          rotationY: state.currentRotation * hoverMultiplier,
          scale: 1 + (state.currentScale - 1) * hoverMultiplier,
          rotation: baseRotation,
          transformPerspective: 1000,
          transformOrigin: 'center center',
        });
      });
    }

    requestAnimationFrame(animate);
  }

  const centerText = document.querySelector('.gallery-text-wrap');
  const bottomText = document.querySelector('.gallery-text-bottom');

  const splitCenter = new SplitText(centerText, {
    type: 'lines',
    mask: 'lines',
  });
  const splitBottom = new SplitText(bottomText, { type: 'lines', mask: 'lines' });

  gsap.set([...splitCenter.lines, ...splitBottom.lines], { yPercent: 100 });

  gsap.to(splitCenter.lines, {
    yPercent: 0,
    stagger: 0.1,
    duration: 1,
    ease: 'power4.out',
    scrollTrigger: {
      trigger: wrapper,
      start: 'center bottom',
      end: 'center top',
      scrub: true,
    },
  });

  //   gsap.to(splitBottom.lines, {
  //     yPercent: 0,
  //     stagger: 0.1,
  //     duration: 1,
  //     ease: 'power4.out',
  //     scrollTrigger: {
  //       trigger: wrapper,
  //       start: 'center bottom',
  //       end: 'center top',
  //       scrub: true,
  //     },
  //   });

  animate();

  gsap.to(gallery, {
    y: '-60vh',
    duration: 1,
    ease: 'power1.inOut',
    scrollTrigger: {
      trigger: wrapper,
      start: '65% center',
      end: '75% center',
      scrub: true,
    },
  });

  gsap.to(centerText, {
    y: '-26vh',
    duration: 1,
    ease: 'power1.inOut',
    scrollTrigger: {
      trigger: wrapper,
      start: '65% center',
      end: '75% center',
      scrub: true,
    },
  });

  gsap.to(splitBottom.lines, {
    yPercent: 0,
    stagger: 0.1,
    ease: 'power4.out',
    duration: 1,
    scrollTrigger: {
      trigger: wrapper,
      start: '70% center',
      end: '80% center',
      scrub: true,
    },
  });

  gsap.to(gallery, {
    rotateZ: 67,
    duration: 1,
    ease: 'none',
    scrollTrigger: {
      trigger: wrapper,
      start: '60% center',
      end: 'bottom center',
      scrub: true,
    },
  });

  gsap.to(block, {
    autoAlpha: 0,
    duration: 1,
    ease: 'power1.inOut',
    scrollTrigger: {
      trigger: wrapper,
      start: '75% center',
      end: 'bottom center',
      scrub: true,
    },
  });
}

export function generatePixels({
  wrapperSelector = '.grid-transition',
  columns = 10,
  rows = 9,
} = {}) {
  const wrapper = document.querySelector(wrapperSelector);
  if (!wrapper) return;

  wrapper.innerHTML = '';
  wrapper.style.setProperty('--cols', columns);
  wrapper.style.setProperty('--rows', rows);

  const total = columns * rows;

  for (let i = 0; i < total; i++) {
    const pixel = document.createElement('div');
    pixel.classList.add('grid-transition-item');
    wrapper.appendChild(pixel);
  }
}

export function initPixelTransition() {
  const wrapper = document.querySelector('.grid-transition');
  if (!wrapper) return;

  const pixels = Array.from(wrapper.querySelectorAll('.grid-transition-item'));
  if (!pixels.length) return;

  const cols = Number(getComputedStyle(wrapper).getPropertyValue('--cols')) || 10;
  const rows = Number(getComputedStyle(wrapper).getPropertyValue('--rows')) || 9;

  // Random order with soft bias
  const shuffledPixels = pixels
    .map((pixel, index) => ({
      pixel,
      sort: Math.random() + index / pixels.length,
    }))
    .sort((a, b) => b.sort - a.sort)
    .map((item) => item.pixel);

  // Reset state
  gsap.set(pixels, { opacity: 0 });

  gsap.to(shuffledPixels, {
    opacity: 1,
    stagger: {
      amount: 1.3,
      grid: [cols, rows],
      axis: 'y',
    },
    ease: 'power2.out',
    scrollTrigger: {
      trigger: wrapper,
      start: 'top center',
      end: 'bottom center',
      scrub: true,
    },
  });
}

export function initWorkHomepage() {
  const workItems = document.querySelectorAll('.work-item');
  if (!workItems.length) return;

  workItems.forEach((item) => {
    const img = item.querySelector('img');

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: item,
        start: 'top bottom',
        end: 'bottom bottom',
        scrub: 1,
      },
    });

    gsap.set(img, { scale: 1.25, transformOrigin: 'center center' });
    gsap.set(item, { clipPath: 'inset(0% 100% 100% 0%)' });

    tl.to(item, {
      clipPath: 'inset(0% 0% 0% 0%)',
      duration: 1,
      ease: 'power1.inOut',
    });

    tl.to(
      img,
      {
        scale: 1,
        duration: 1,
        ease: 'power1.inOut',
      },
      '<'
    );
  });
}

export function initPixelSection() {
  // Bisa kamu atur bebas
  generatePixels({
    columns: 16,
    rows: 12,
  });

  initPixelTransition();

  // Wajib kalau pakai ScrollTrigger
  ScrollTrigger.refresh();
}

export function initServiceHover() {
  const wrapper = document.querySelector('.service-wrapper');
  const serviceItems = document.querySelectorAll('.service-item');
  if (!serviceItems.length) return;

  serviceItems.forEach((item) => {
    const content = item.querySelector('.service-item-content');
    const imgContents = item.querySelectorAll('.service-item-img');

    const trigger = item.querySelector('.service-item-trigger');
    const title = item.querySelector('.tittle-wrap');

    const text = item.querySelector('.service-text');
    const textAbsolute = item.querySelector('.service-text.is-absolute');

    const splitText = new SplitText(text, { type: 'chars, words' });
    const splitTextAbsolute = new SplitText(textAbsolute, { type: 'chars, words' });

    const tl = gsap.timeline({ paused: true });

    gsap.set(imgContents, { scale: 0, autoAlpha: 0 });
    gsap.set(title, { autoAlpha: 0 });
    gsap.set([...splitText.chars, ...splitTextAbsolute.chars], { yPercent: 100 });

    tl.to(
      splitText.chars,
      {
        yPercent: -100,
        stagger: {
          amount: 0.2,
          from: 'start',
        },
        duration: 0.5,
        ease: 'custom',
      },
      0
    )
      .to(
        splitTextAbsolute.chars,
        {
          yPercent: -100,
          stagger: {
            amount: 0.2,
            from: 'start',
          },
          duration: 0.5,
          ease: 'custom',
        },
        0
      )
      .to(
        imgContents,
        {
          scale: 1,
          autoAlpha: 1,
          duration: 0.5,
          stagger: { amount: 0.4, from: 'random' },
          ease: 'custom',
        },
        0
      )
      .to(title, { autoAlpha: 1, duration: 0.5, ease: 'custom' }, 0);

    trigger.addEventListener('mouseenter', () => {
      tl.play();
    });

    trigger.addEventListener('mouseleave', () => {
      tl.reverse();
    });

    const maxMove = 32; // maksimal gerak (px)

    const moveX = gsap.quickTo(content, 'x', {
      duration: 0.8,
      ease: 'power2.out',
    });

    const moveY = gsap.quickTo(content, 'y', {
      duration: 0.8,
      ease: 'power2.out',
    });

    trigger.addEventListener('mousemove', (e) => {
      const bounds = trigger.getBoundingClientRect();

      const relX = e.clientX - bounds.left;
      const relY = e.clientY - bounds.top;

      const percentX = (relX / bounds.width - 0.5) * 2;
      const percentY = (relY / bounds.height - 0.5) * 2;

      moveX(percentX * maxMove);
      moveY(percentY * maxMove);
    });

    trigger.addEventListener('mouseleave', () => {
      moveX(0);
      moveY(0);
    });

    gsap.to(splitText.chars, {
      yPercent: 0,
      stagger: {
        amount: 0.4,
        from: 'start',
      },
      duration: 0.8,
      ease: 'power4.out',
      scrollTrigger: {
        trigger: wrapper,
        start: 'top center',
        end: 'bottom center',
      },
    });
  });
}

export function initAwwardAnimation() {
  const block = document.querySelector('.award-block');
  if (!block) return;

  const wrapper = block.querySelector('.award-wrapper');
  const items = block.querySelectorAll('.award-content-item');
  const itemsTop = block.querySelectorAll('.award-content-item.is-top');
  const itemsBottom = block.querySelectorAll('.award-content-item.is-bottom');
  const tittle = block.querySelector('.tittle-wrap');
  const head = block.querySelector('.award-heading');

  const itemHeight = items[0]?.getBoundingClientRect().height || 0;
  const windowHeight = window.innerHeight;

  const splitHead = new SplitText(head, {
    type: 'chars, words',
    mask: 'words',
  });

  const tl = gsap.timeline({
    scrollTrigger: {
      trigger: block,
      start: 'top top',
      end: 'bottom center',
      scrub: 1,
    },
  });

  gsap.set(itemsTop, {
    y: -windowHeight,
  });

  gsap.set(itemsBottom, {
    y: itemHeight,
  });

  gsap.set(tittle, {
    autoAlpha: 0,
  });

  gsap.set(splitHead.chars, {
    yPercent: 100,
  });

  gsap.set(wrapper, {
    // scale: 0.2,
    clipPath: 'inset(0% 50%)',
  });

  gsap.to(wrapper, {
    // scale: 1,
    clipPath: 'inset(0% 0%)',

    // transformOrigin: 'center center',
    ease: 'none',
    scrollTrigger: {
      trigger: block,
      start: 'top bottom',
      end: 'top top',
      scrub: true,
    },
  });

  // Animasi utama
  tl.addLabel('reveal')
    // .to(
    //   wrapper,
    //   {
    //     // scale: 1,
    //     clipPath: 'circle(150% at center center)',

    //     // transformOrigin: 'center center',
    //     ease: 'power1.inOut',
    //   },
    //   'reveal'
    // )

    .to(
      splitHead.chars,
      {
        yPercent: 0,
        stagger: {
          amount: 0.2,
        },
        duration: 0.2,
        ease: 'power1.inOut',
      },
      'reveal +=0'
    )
    .to(
      tittle,
      {
        autoAlpha: 1,
        duration: 0.2,
        ease: 'power1.inOut',
      },
      'reveal +=0'
    );
  tl.addLabel('itemsIn')
    .to(
      itemsTop,
      {
        y: itemHeight,
        duration: 1,
        ease: 'power1.inOut',
      },
      'itemsIn'
    )
    .to(
      itemsBottom,
      {
        y: -windowHeight,
        duration: 1,
        ease: 'power1.inOut',
      },
      'itemsIn'
    )
    .to(
      head,
      {
        scale: 0.667,
        duration: 1,
        ease: 'power1.inOut',
      },
      'itemsIn'
    );

  gsap.fromTo(
    [head, tittle, ...items],
    {
      opacity: 1,
    },
    {
      opacity: 0,
      duration: 0.5,
      ease: 'power1.inOut',
      scrollTrigger: {
        trigger: block,
        start: '90% bottom',
        end: 'bottom 60%',
        scrub: 1,
      },
    }
  );

  // tl.addLabel('out')
  //   .to(
  //     head,
  //     {
  //       autoAlpha: 0,
  //       duration: 0.5,
  //       ease: 'power1.inOut',
  //     },
  //     'out'
  //   )
  //   .to(
  //     tittle,
  //     {
  //       autoAlpha: 0,
  //       duration: 0.5,
  //       ease: 'power1.inOut',
  //     },
  //     'out'
  //   );
}

export function initSectionOverlapFull() {
  gsap.utils.toArray('[data-overlap-full]').forEach((currentSection) => {
    const previousSection = currentSection.previousElementSibling;
    if (!previousSection) return;

    // Buat overlay di section sebelumnya
    const overlay = document.createElement('div');
    overlay.className = 'dark-section-overlay';
    previousSection.appendChild(overlay);

    const getOffset = () => window.innerHeight;

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: currentSection,
        start: 'top bottom',
        end: 'top top',
        scrub: true,
        // onRefresh: (st) => {
        //   st.end = `bottom+=${getOffset()}px top`;
        // },
      },
    });

    tl.to(previousSection, {
      y: () => getOffset(),
      // y: '100vh',
      rotate: 0.001,
      ease: 'none',
    }).to(
      overlay,
      {
        opacity: 1,
        ease: 'none',
      },
      0
    );

    ScrollTrigger.create({
      trigger: currentSection,
      start: 'top bottom',
      end: 'top top',
      onEnter: () => {
        gsap.set(currentSection, { zIndex: 5 });
        gsap.set(previousSection, { zIndex: 4 });
      },
      onLeaveBack: () => {
        gsap.set(currentSection, { zIndex: 'auto' });
        gsap.set(previousSection, { zIndex: 'auto' });
      },
    });
  });
}

export function initSectionOverlapNext() {
  gsap.utils.toArray('[data-overlap-next]').forEach((currentSection) => {
    const nextSection = currentSection.nextElementSibling;
    if (!nextSection) return;

    // Buat overlay di section sebelumnya
    const overlay = document.createElement('div');
    overlay.className = 'g_section-overlay';
    nextSection.appendChild(overlay);

    const getOffset = () => -(window.innerHeight / 4);

    // gsap.set(nextSection, { y: getOffset() });

    const tl = gsap.timeline({
      scrollTrigger: {
        trigger: currentSection,
        start: 'bottom bottom',
        end: 'bottom top',
        scrub: true,
        onRefresh: (st) => {
          st.end = `+=${getOffset()}px`;
        },
      },
    });

    tl.from(nextSection, {
      y: getOffset(),
      rotate: 0.001,
      ease: 'none',
    }).from(
      overlay,
      {
        opacity: 1,
        ease: 'none',
      },
      0
    );

    ScrollTrigger.create({
      trigger: currentSection,
      start: 'bottom bottom',
      end: 'bottom top',
      onEnter: () => {
        gsap.set(nextSection, { zIndex: 0 });
        gsap.set(currentSection, { zIndex: 1 });
      },
      onLeaveBack: () => {
        gsap.set(nextSection, { zIndex: 0 });
        gsap.set(currentSection, { zIndex: 1 });
      },
    });
  });
}

