export function reviewMarquee() {
  const wrapper = document.querySelector('.review-heading');
  if (!wrapper) return;

  const marquee = wrapper.querySelector('.running-scrub-wrap');

  const contentWidth = marquee.scrollWidth;
  const moveWidth = contentWidth / 3;

  gsap.set(marquee, { x: '10vw' });

  const tl = gsap.timeline({
    scrollTrigger: {
      trigger: wrapper,
      start: 'top bottom',
      end: 'bottom top',
      scrub: true,
    },
  });

  tl.to(marquee, {
    x: -moveWidth,
    duration: 1,
    ease: 'none',
  });
}

