export function initSwiper() {
  const item = document.querySelector('.swiper.is-review');
  if (!item) return;

  const swiper = new Swiper(item, {
    slidesPerView: 2.5,
    spaceBetween: 16,
    centeredSlides: false,
  });
}

export function navControl() {
  const navbar = document.querySelector('.navbar');
  if (!navbar) return;

  ScrollTrigger.create({
    trigger: navbar,
    start: 'bottom top',
    onEnter: () => {
      navbar.classList.add('is-fixed');
      gsap.to(navbar, { autoAlpha: 0, yPercent: -100, duration: 0.3 });
    },
    onLeaveBack: () => {
      navbar.classList.remove('is-fixed');
      gsap.to(navbar, { autoAlpha: 1, yPercent: 0, duration: 0.3 });
    },
    markers: true,
  });
}
