export function initCounter() {
  const counters = document.querySelectorAll('[data-counter]');
  if (!counters.length) return;

  counters.forEach((el) => {
    // Ambil target dari text span
    const target = parseFloat(el.textContent.replace(/,/g, '')) || 0;

    // Optional configs
    const duration = parseFloat(el.dataset.counterSpeed) || 2; // [data-counter-speed] in seconds
    const start = parseFloat(el.dataset.counterStart) || 0; // [data-counter-start]
    const decimals = parseInt(el.dataset.counterDecimals) || 0; // [data-counter-decimals] = true or false

    // Set initial text
    el.textContent = start.toLocaleString(undefined, {
      minimumFractionDigits: decimals,
      maximumFractionDigits: decimals,
    });

    const counterObj = { value: start };

    gsap.to(counterObj, {
      value: target,
      duration,
      ease: 'power1.inOut',
      scrollTrigger: {
        trigger: el,
        start: 'top bottom',
        once: true,
      },
      onUpdate() {
        el.textContent = counterObj.value.toLocaleString(undefined, {
          minimumFractionDigits: decimals,
          maximumFractionDigits: decimals,
        });
      },
    });
  });
}
