/**
 * Animasi teks per baris menggunakan GSAP SplitText.
 * Secara otomatis mencari elemen dengan atribut [data-split-lines].
 *
 * @param {Object} options
 * @param {HTMLElement|Document} [options.scope=document] - Scope pencarian elemen (default: seluruh dokumen)
 */
export function splitLines({ scope = document } = {}) {
  const splitElements = scope.querySelectorAll('[data-split-lines]');

  splitElements.forEach((container) => {
    let elements = [];
    const splitRefs = [];
    const lines = [];

    const delay = parseFloat(container.getAttribute('data-delay')) || 0;
    const scrollAttr = container.getAttribute('data-scroll');
    const animateOnScroll = scrollAttr !== 'false'; // default: true

    // Jika elemen punya data-copy-wrapper → targetkan anak-anaknya
    if (container.hasAttribute('data-copy-wrapper')) {
      elements = Array.from(container.children);
    } else {
      elements = [container];
    }

    elements.forEach((element) => {
      const split = SplitText.create(element, {
        type: 'lines',
        mask: 'lines',
        linesClass: 'line++',
        lineThreshold: 0.1,
      });

      splitRefs.push(split);

      // Tangani text-indent agar tidak hilang
      const computedStyle = window.getComputedStyle(element);
      const { textIndent } = computedStyle;
      if (textIndent && textIndent !== '0px') {
        if (split.lines.length > 0) {
          split.lines[0].style.paddingLeft = textIndent;
        }
        element.style.textIndent = '0';
      }

      lines.push(...split.lines);
    });

    // Set posisi awal: turun 100%
    gsap.set(lines, { y: '100%' });

    const animationProps = {
      y: '0%',
      duration: 1,
      stagger: 0.1,
      ease: 'power4.out',
      delay,
    };

    if (animateOnScroll) {
      gsap.to(lines, {
        ...animationProps,
        scrollTrigger: {
          trigger: container,
          start: 'top 75%',
          once: true,
        },
      });
    } else {
      gsap.to(lines, animationProps);
    }

    // Optional: return fungsi cleanup
    return () => splitRefs.forEach((s) => s?.revert());
  });
}

export function initSplitLines() {
  const splitTargets = document.querySelectorAll('[data-split]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'lines',
      mask: 'lines',
      linesClass: 'line',
    });

    gsap.set(split.lines, { yPercent: 101 });

    ScrollTrigger.create({
      trigger: el,
      start: 'top 90%',
      markers: false,
      onEnter: () => {
        gsap.to(split.lines, {
          yPercent: 0,
          duration: 1.25,
          ease: 'primary-ease-out',
          stagger: {
            amount: 0.2,
            from: 'start',
          },
        });
      },
    });
  });
}

export function initSplitHead() {
  const splitTargets = document.querySelectorAll('[data-heading]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'chars, words, lines',
      mask: 'words',
      wordsClass: 'word',
      charsClass: 'char',
      linesClass: 'line',
    });

    gsap.set(split.chars, { yPercent: 101 });

    ScrollTrigger.create({
      trigger: el,
      start: 'top 90%',
      markers: false,
      onEnter: () => {
        gsap.to(split.chars, {
          yPercent: 0,
          duration: 1.25,
          ease: 'primary-ease-out',
          stagger: {
            amount: 0.2,
            from: 'start',
          },
        });
      },
    });
  });
}

export function createSplitLines(targets, options = {}) {
  const {
    yStart = '100%',
    yEnd = '0%',
    duration = 1.2,
    ease = 'power4.out',
    stagger = 0.05,
    delay = 0,
  } = options;

  // pastikan targets bisa NodeList / Array / Element
  const elements = targets instanceof Element ? [targets] : Array.from(targets || []);

  const splitRefs = [];
  const allLines = [];

  elements.forEach((el) => {
    const split = new SplitText(el, {
      type: 'lines',
      mask: 'lines',
      linesClass: 'line++',
      lineThreshold: 0.1,
    });
    splitRefs.push(split);
    gsap.set(split.lines, { yPercent: 100 });
    allLines.push(...split.lines);
  });

  // buat timeline reusable
  const tl = gsap.timeline({ delay });
  tl.to(allLines, {
    yPercent: 0,
    duration,
    ease,
    stagger,
  });

  // return biar bisa .add() ke timeline utama
  return {
    lines: allLines,
    timeline: tl,
    revert: () => splitRefs.forEach((s) => s?.revert()),
  };
}

export function createSplitText() {
  const splitTargets = document.querySelectorAll('[data-create-split]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'chars, words, lines',
      mask: 'lines',
      wordsClass: 'word',
      charsClass: 'char',
      linesClass: 'line',
    });
  });
}

export function initFadeEatchWordOnScroll() {
  const splitTargets = document.querySelectorAll('[data-fade-each-word]');
  if (!splitTargets.length) return;

  splitTargets.forEach((el) => {
    if (!el || typeof el !== 'object' || !el.textContent.trim()) return;

    const split = new SplitText(el, {
      type: 'words',
      wordsClass: 'word',
    });

    gsap.set(split.words, { autoAlpha: 0.16 });
    gsap.to(split.words, {
      autoAlpha: 1,
      duration: 1,
      stagger: {
        each: 0.5,
        from: 'start',
      },
      ease: 'none',
      scrollTrigger: {
        trigger: el,
        start: 'top 90%',
        end: 'bottom center',
        scrub: true,
      },
    });
  });
}

export function createGradientHeadingTimeline(wrapper, options = {}) {
  if (!wrapper) return gsap.timeline();

  const {
    gradient = wrapper.getAttribute('data-gradient') ||
      'linear-gradient(177deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.5) 12.35%, white 48.75%, rgba(255, 255, 255, 0.5) 100%)',
    stagger = 0.05,
    duration = 0.8,
    ease = 'power3.out',
  } = options;

  const textElements = wrapper.querySelectorAll('[data-gradient-heading]');
  if (!textElements.length) return gsap.timeline();

  const splits = [];
  const allWords = [];

  textElements.forEach((el) => {
    const split = new SplitText(el, {
      type: 'words',
      wordsClass: 'word',
    });
    splits.push(split);
    allWords.push(...split.words);
  });

  // ---- gradient helper
  function applySeamlessGradient() {
    const wrapperRect = wrapper.getBoundingClientRect();
    const wrapperWidth = wrapperRect.width;

    allWords.forEach((word) => {
      const wordRect = word.getBoundingClientRect();
      const offsetX = wordRect.left - wrapperRect.left;

      word.style.backgroundImage = gradient;
      word.style.backgroundSize = `${wrapperWidth}px 100%`;
      word.style.backgroundPosition = `-${offsetX}px 0`;
      word.style.webkitBackgroundClip = 'text';
      word.style.backgroundClip = 'text';
      word.style.webkitTextFillColor = 'transparent';
      word.style.color = 'transparent';
      word.style.display = 'inline-block';
    });
  }

  // initial
  applySeamlessGradient();

  gsap.set(allWords, {
    yPercent: 100,
    opacity: 0,
  });

  // ---- TIMELINE
  const tl = gsap.timeline({
    onStart: applySeamlessGradient,
    onComplete: applySeamlessGradient,
  });

  tl.to(allWords, {
    yPercent: 0,
    opacity: 1,
    duration,
    stagger,
    ease,
  });

  // resize safety
  let resizeTimeout;
  window.addEventListener('resize', () => {
    clearTimeout(resizeTimeout);
    resizeTimeout = setTimeout(applySeamlessGradient, 100);
  });

  return tl;
}
